@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@900&display=swap);
body {
  margin: 0;
  padding: 0;
  
}

@font-face {
  font-family: EngraversGothic BT;
  src: url(/Vintage-Cars-Display/static/media/Engravers-Gothic-BT-Font.be89a5be.ttf);
}

@font-face {
  font-family: Bodoni 72 Oldstyle;
  src: url("/Vintage-Cars-Display/static/media/Bodoni BE Oldstyle Figures Bold.51d79b91.otf");
}

.App {
  text-align: center;
  font-family: EngraversGothic BT;
}

.mainBackground{
   width: 50% !important;
   background-color: #000;
   border-radius: 20px !important;
   box-shadow: black;
   height: auto;
   margin-top: 11%
}

.head{
font-weight: bolder;
font-size: xxx-large;
color: #FFFFFF;
mix-blend-mode: normal;
opacity: 0.3;
position: absolute;
margin-left: 5%;
font-family: 'Merriweather', serif !important;
z-index: 4000;
letter-spacing: 6px;
}

.miniText{
  font-weight: bolder;
  color: #FFFFFF;
  position: absolute;
  margin-left: 5%;
  margin-top: 5rem;
  font-size: xxx-large;
  text-align: left;
}

.padded{
  padding-top: -3px;
}

.img{
  background-color: #000 !important;
  background: #000 !important;
  margin-top: -2rem !important;
}

.owl-dots
  {
    display: inline-grid !important;
    margin-left: -45% !important;
    Padding-top: -32% !important;
    position: absolute !important;
    margin-top: -15% !important;
 
}

.owl-theme .owl-dots .owl-dot span {
  width: 71px !important;
  height: 10px !important;
  margin: 5px 7px;
  background: #D6D6D6;
  display: block;
  -webkit-backface-visibility: visible;
  transition: opacity 200ms ease;
  border-radius: 0px !important;
}

.owl-theme .owl-nav{
    margin-top: -7% !important;
    text-align: center;
    position: absolute !important;
    -webkit-tap-highlight-color: transparent;
    margin-left: 5%;
}

.owl-theme .owl-nav [class*='owl-'] {
  color: #FFF !important;
  font-size: 14px;
  margin: 5px;
  padding: 4px 7px;
  background: #D6D6D6;
  display: inline-block;
  cursor: pointer;
  border-radius: 3px;
}

.owl-theme .owl-nav .owl-prev span .left {
  background: url(data:image/png;base64,) !important;
  background-repeat: no-repeat !important;
  background-size: contain !important;
  height: 31px;
  width: 31px;
  border: none !important;
}

.owl-theme .owl-nav .owl-next span .right {
  background: url(data:image/png;base64,) !important;
  background-repeat: no-repeat !important;
  background-size: contain !important;
  height: 31px;
  width: 31px;
  border: none !important;
}

@media (min-width:300px) and (max-width: 500px){
  .head{
    font-weight: bolder;
    font-size: xxx-large;
    color: #FFFFFF;
    mix-blend-mode: normal;
    opacity: 0.3;
    position: absolute;
    margin-left: 5%;
    font-family: 'Merriweather', serif !important;
    z-index: 4000;
    letter-spacing: 6px;
    }
    
    .miniText{
      font-weight: bolder;
      color: #FFFFFF;
      position: absolute;
      margin-left: 5%;
      margin-top: 5rem;
      font-size: large;
      text-align: left;
    }
}
.bc{
    background-color: black !important;
}

.box{
    border-bottom: 1px solid rgb(134, 133, 133) !important;

}

.white{
    letter-spacing: 2.84143px;
    color: #FFFFFF !important;
    font-size: 12px !important;
}

